import React, { useState } from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';

import { BCard, BContainer, BTable } from '../../components';
import { parks } from '../../fakeData/parks';

const columns = [{ key: 'pm_name', title: '', style: { width: '10%' } },
{ key: 'pm_name_eng', title: '', style: { width: '10%' } },
{ key: 'pm_construction', title: '', style: { width: '10%' } },
{ key: 'pm_unit', title: '', style: { width: '20%' } },
{ key: 'pm_service', title: '', style: { width: '20%' } }];

export const Parks = () => {
  const [dataSource, setDataSource] = useState(parks.slice(0 * 10, (0 + 1) * 10));

  return (
    <BContainer fluid={true}>
      <BCard title=''>
        <FormGroup row>
          <Label htmlFor="pm_name" sm={2}></Label>
          <Col sm="4">
            <Input
              type="text"
              name="pm_name"
              id="pm_name"
              placeholder=""
            />
          </Col>
          <Col sm="4">

          </Col>
        </FormGroup>
      </BCard>
      <BTable
        columns={columns}
        dataSource={dataSource}
        dataCount={parks.length}
        pageChange={(pageIndex) => { setDataSource(parks.slice(pageIndex * 10, (pageIndex + 1) * 10)); }}
      ></BTable>
    </BContainer>
  )
}